y: var(--color-white-100);\r\n  --color-text-highlight: #000;\r\n}\r\n","@font-face {\r\n  font-family: \"Satoshi\";\r\n  font-weight: 400;\r\n  src: url(../fonts/Satoshi-Regular.woff2) format(woff2),\r\n    url(../fonts/Satoshi-Regular.woff) format(woff);\r\n  // src: url(../../assets/fonts/Satoshi-Regular.woff2) format(woff2),\r\n  //   url(../../assets/fonts/Satoshi-Regular.woff) format(woff);\r\n}\r\n\r\n@font-face {\r\n  font-family: \"Satoshi\";\r\n  font-weight: 700;\r\n  src: url(../fonts/Satoshi-Bold.woff2) format(woff2),\r\n    url(../fonts/Satoshi-Bold.woff) format(woff);\r\n}\r\n",":root {\r\n  --font-fallback: \"Satoshi\", sans-serif;\r\n  --font-family-body: var(--font-fallback);\r\n  --font-family-heading: var(--font-fallback);\r\n\r\n  --font-size-900: 3.052rem;\r\n  --font-size-800: 2.441rem;\r\n  --font-size-700: 1.953rem;\r\n  --font-size-600: 1.563rem;\r\n  --font-size-500: 1.25rem;\r\n  --font-size-400: 1rem;\r\n  --font-size-300: 0.8rem;\r\n\r\n  --font-size-body: var(--font-size-400);\r\n  --font-size-h1: var(--font-size-800);\r\n  --font-size-h2: var(--font-size-600);\r\n  --font-size-h3: var(--font-size-500);\r\n  --font-size-small: var(--font-size-300);\r\n  --line-height-body: 1.75;\r\n  --line-height-heading: 1.3;\r\n\r\n  --font-type-body: 400 var(--font-size-body) / var(--line-height-body)\r\n    var(--font-family-body);\r\n  --font-type-h1: 700 var(--font-size-h1) / var(--line-height-heading)\r\n    var(--font-family-heading);\r\n  --font-type-h2: 700 var(--font-size-h2) / var(--line-height-heading)\r\n    var(--font-family-heading);\r\n  --font-type-h3: 700 var(--font-size-h3) / var(--line-height-heading)\r\n    var(--font-family-heading);\r\n}\r\n",":root {\r\n  --border-radius-400: 0.875rem;\r\n  --border-radius-300: 0.75rem;\r\n  --border-radius-200: 0.5rem;\r\n\r\n  --button-height: 2.25rem;\r\n\r\n  --text-input-height: 42px;\r\n\r\n  --transition-popup: 0.3s cubic-bezier(0.3, 0, 0.2, 1);\r\n  --transition-popup-open: 120ms ease-out;\r\n}\r\n",".popup {\n  display: none;\n  flex-direction: column;\n  justify-content: flex-end;\n  position: fixed;\n  inset: 0;\n  z-index: 100;\n\n  height: 100%;\n\n  pointer-events: none;\n}\n\n.popup_opened {\n  pointer-events: auto;\n\n  & .popup__overlay {\n    opacity: 1;\n  }\n\n  & .popup__content {\n    transform: translateY(0);\n  }\n}\n\n.popup__overlay {\n  position: fixed;\n  inset: 0;\n\n  background: var(--color-bg-overlay);\n\n  opacity: 0;\n\n  transition: opacity var(--transition-popup);\n}\n\n.popup__content {\n  position: relative;\n  overflow: auto;\n\n  width: 100%;\n  height: auto;\n  max-height: calc(100% - 3rem);\n  background: var(--color-bg-secondary);\n\n  transform: translateY(100%);\n  border-radius: var(--border-radius-400) var(--border-radius-400) 0 0;\n\n  scroll-behavior: smooth;\n\n  transition: transform var(--transition-popup);\n}\n","/* -------------------------------------------------------------------------- */\r\n/*                                   SEARCH                                   */\r\n/* -------------------------------------------------------------------------- */\r\n\r\n.recipes-search-container {\r\n  display: grid;\r\n  row-gap: 10px;\r\n\r\n  padding: 1rem 0;\r\n}\r\n\r\n.ingredients-search {\r\n  display: grid;\r\n  grid-template-columns: 1fr auto;\r\n  grid-template-rows: auto auto;\r\n  column-gap: 0.75rem;\r\n  row-gap: 0.75rem;\r\n\r\n  width: 100%;\r\n\r\n  &__selected-ingredients {\r\n    grid-column: 1 / 3;\r\n  }\r\n}\r\n\r\n.ingredients-search__form {\r\n  display: flex;\r\n  flex-direction: column;\r\n  position: relative;\r\n\r\n  height: var(--text-input-height);\r\n}\r\n\r\n.ingredients-search__input-container {\r\n  position: relative;\r\n\r\n  height: 100%;\r\n}\r\n\r\n.ingredients-search__search-icon {\r\n  position: absolute;\r\n  top: 0;\r\n  bottom: 0;\r\n\r\n  margin: auto;\r\n  margin-left: 0.75rem;\r\n\r\n  fill: #fff;\r\n  pointer-events: none;\r\n}\r\n\r\n.ingredients-search__input {\r\n  width: 100%;\r\n  height: 100%;\r\n  border: none;\r\n  outline: none;\r\n  background: var(--color-bg-secondary);\r\n\r\n  padding: 0 0.8rem;\r\n  padding-left: 2.75rem;\r\n\r\n  border-radius: var(--border-radius-200);\r\n\r\n  font: inherit;\r\n  color: inherit;\r\n\r\n  &::placeholder {\r\n    font: var(--font-type-body);\r\n    color: var(--color-text-tertiary);\r\n  }\r\n}\r\n\r\n/* -------------------------------------------------------------------------- */\r\n/*                        INGREDIENT SEARCH SUGGESTIONS                       */\r\n/* -------------------------------------------------------------------------- */\r\n\r\n.ingredients-search__suggestions {\r\n  display: none;\r\n  position: absolute;\r\n  top: calc(100% + 0.5rem);\r\n  overflow: hidden;\r\n  z-index: 10;\r\n\r\n  width: 100%;\r\n\r\n  background: var(--color-bg-secondary);\r\n\r\n  font: var(--font-type-body);\r\n  border-radius: var(--border-radius-200);\r\n}\r\n\r\n.ingredients-search__suggestions_show {\r\n  display: block;\r\n}\r\n\r\n.ingredients-search__suggestions-list {\r\n  max-height: calc(var(--text-input-height) * 5);\r\n  margin: 0;\r\n  padding: 0;\r\n  overflow: auto;\r\n\r\n  text-indent: 0;\r\n  list-style-type: none;\r\n}\r\n\r\n.ingredients-search__suggestions-item {\r\n  display: flex;\r\n  align-items: center;\r\n\r\n  padding: 0 0.8rem;\r\n  height: var(--text-input-height);\r\n\r\n  &_active {\r\n    background: var(--color-bg-tertiary);\r\n  }\r\n}\r\n\r\n/* -------------------------------------------------------------------------- */\r\n/*                          SELECTED INGREDIENTS MENU                         */\r\n/* -------------------------------------------------------------------------- */\r\n\r\n.selected-ingredients-menu {\r\n  width: 100%;\r\n  background: var(--color-bg-secondary);\r\n  // border: 2px solid var(--color-bg-tertiary);\r\n\r\n  border-radius: var(--border-radius-200);\r\n\r\n  cursor: pointer;\r\n  -webkit-tap-highlight-color: rgba(255, 255, 255, 0);\r\n\r\n  &_active {\r\n    .selected-ingredients-menu__body {\r\n      grid-template-rows: 1fr;\r\n\r\n      border-width: 2px;\r\n\r\n      transition: grid-template-rows 0.2s;\r\n    }\r\n\r\n    .selected-ingredients-menu__arrow-down-icon {\r\n      transform: rotateX(180deg);\r\n\r\n      transition: transform 0.2s;\r\n    }\r\n  }\r\n}\r\n\r\n.selected-ingredients-menu__header {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n\r\n  padding: 0 0.75rem;\r\n\r\n  min-height: 42px;\r\n}\r\n\r\n.selected-ingredients-menu__arrow-down-icon {\r\n  fill: #fff;\r\n  transition: transform 0.3s;\r\n}\r\n\r\n.selected-ingredients-menu__body {\r\n  display: grid;\r\n  grid-template-rows: 0fr;\r\n  overflow: hidden;\r\n\r\n  padding: 0 0.5rem;\r\n  border-top: solid var(--color-bg-primary);\r\n  border-width: 0;\r\n\r\n  transition: grid-template-rows 0.2s, border 0s 0.2s;\r\n}\r\n\r\n.selected-ingredients-menu__content {\r\n  overflow: hidden;\r\n}\r\n\r\n.selected-ingredients-menu__list {\r\n  display: flex;\r\n  flex-wrap: wrap;\r\n  gap: 0.5rem;\r\n\r\n  padding: 0.5rem 0;\r\n  margin: 0;\r\n\r\n  text-indent: 0;\r\n  list-style-type: none;\r\n\r\n  &[empty] {\r\n    .selected-ingredients-menu__list-message {\r\n      display: block;\r\n    }\r\n  }\r\n}\r\n\r\n.selected-ingredients-menu__item {\r\n  display: flex;\r\n  align-items: center;\r\n  gap: 0.5rem;\r\n\r\n  min-height: 36px;\r\n  padding: 0 0.75rem;\r\n  background: var(--color-bg-tertiary);\r\n  // border: 2px solid var(--color-white-400);\r\n\r\n  border-radius: 18px;\r\n  font: var(--font-type-body);\r\n\r\n  &-xmark-icon {\r\n    position: relative;\r\n    top: 1px;\r\n\r\n    fill: #fff;\r\n  }\r\n}\r\n\r\n.selected-ingredients-menu__list-message {\r\n  display: none;\r\n\r\n  margin: 0 auto;\r\n  padding: 0;\r\n\r\n  color: var(--color-text-secondary);\r\n}\r\n","/* -------------------------------------------------------------------------- */\r\n/*                                   RECIPES                                  */\r\n/* -------------------------------------------------------------------------- */\r\n\r\n.recipe-search-button {\r\n  padding: 0.5rem;\r\n  background: var(--color-bg-tertiary);\r\n  border: none;\r\n\r\n  border-radius: var(--border-radius-200);\r\n\r\n  color: inherit;\r\n  font: inherit;\r\n  font-weight: bold;\r\n}\r\n\r\n.recipes__cards-list {\r\n  display: flex;\r\n  flex-direction: column;\r\n  row-gap: 0.75rem;\r\n\r\n  width: 100%;\r\n}\r\n\r\n/* -------------------------------------------------------------------------- */\r\n/*                                 RECIPE CARD                                */\r\n/* -------------------------------------------------------------------------- */\r\n\r\n.recipe-card {\r\n  width: 100%;\r\n  background: var(--color-bg-secondary);\r\n\r\n  border-radius: var(--border-radius-400);\r\n}\r\n\r\n.recipe-card__img-wrapper {\r\n  height: 55.5vw;\r\n  overflow: hidden;\r\n  padding: 0.25rem;\r\n\r\n  border-radius: var(--border-radius-400);\r\n}\r\n\r\n.recipe-card__img {\r\n  width: 100%;\r\n  height: 100%;\r\n  object-fit: cover;\r\n\r\n  border-radius: var(--border-radius-300);\r\n}\r\n\r\n.recipe-card__description {\r\n  display: flex;\r\n  flex-direction: column;\r\n  row-gap: 1rem;\r\n\r\n  padding: 0.5rem 1rem;\r\n}\r\n\r\n.recipe-card__title {\r\n  font: var(--font-type-h3);\r\n  color: var(--color-text-secondary);\r\n}\r\n\r\n.recipe-card__cooking-time-value {\r\n  font-weight: 700;\r\n}\r\n\r\n/* -------------------------------------------------------------------------- */\r\n/*                                   RECIPE                                   */\r\n/* -------------------------------------------------------------------------- */\r\n.recipe {\r\n  position: fixed;\r\n  inset: 0;\r\n  overflow: auto;\r\n\r\n  background: var(--color-bg-primary);\r\n\r\n  transform: translateX(100%);\r\n\r\n  transition: transform 0.3s;\r\n